/* Basic resets, fonts, and app background setup */
body, h1, p, input, textarea, button {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.App {
  color: white;
  background-color: #1a4731;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Profile container and image styling */
.profile-container {
  width: 90%;
  max-width: 500px;
  padding: 20px;
  border-radius: 10px;
  background-color: #2a5a40;
}

.profile-header .profile-pic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 20px auto;
  display: block;
  background-color: #fff; /* Placeholder if image fails to load */
}

/* Styling for profile fields */
.profile-field {
  margin: 10px 0;
}

.profile-field label {
  display: block;
  font-weight: bold;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: none;
  border-radius: 5px;
  background-color: #3d6b52;
  color: white;
}

button {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  background-color: #4c7a63;
  color: white;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #5e8a74;
}
